import React from 'react'
import * as body from 'react-bootstrap'
import {  Link } from 'react-router-dom'
import AppContext from './context'

export default function ProductCard(props) {
    const context = React.useContext(AppContext)
    return (
        <>
            <div className='p-3'>
                <body.Card
                    className='shadow-sm'
                    style={{
                        width: '11rem',
                    }}
                >
                    <body.Card.Body>
                        <Link variant="primary" to={`/product/${props.products.id}`} className='btn btn-sm btn-primary' style={{
                            position: 'absolute',
                            right: '2px',
                            top: '2px',
                        }}
                            onMouseUp={() => context.viewRecent(props.products.id)}
                        >Details</Link>
                        <body.Card.Img variant='top' src={`${process.env.PUBLIC_URL}/media/products/${props.products.filename}-1.png`} className=''/>
                        <body.Card.Subtitle className='mb-2' style={{
                            paddingTop: '1rem'
                        }}>{props.products.name}</body.Card.Subtitle>
                        <body.Card.Text>${props.products.price}</body.Card.Text>
                    </body.Card.Body>
                </body.Card>
            </div>
                    
        </>     
    )
}