import React, { useState } from 'react'
import * as body from 'react-bootstrap'
import { useRouteMatch } from 'react-router-dom'
import NotFound from './not-found'
import AppContext from './context'
import {Link} from 'react-router-dom'

export default function Detail() {
    let match = useRouteMatch({path: '/product/:id', strict: true, sensitive: true})
    const context = React.useContext(AppContext)
    let p = []
    p = context.products.find(x => x.id.toString() === match.params.id)
    let [pic, setPic] = useState('-1')

    if (!p) {
        return (
            <NotFound />
        )
    }
    else {
        return (
            <body.Container>
                <div className='py-4' style={{
                    paddingLeft: '2rem'
                }}>
                    <div className='float-right'>
                        <img src={`${process.env.PUBLIC_URL}/media/products/${p.filename}${pic}.png`} className='border rounded shadow-sm' style={{
                            width: '300px',
                            marginLeft: '1rem'
                        }} alt={p.name}/>
                        <div align='left' style={{
                            marginTop: '0.5rem'
                        }}>
                            <img className='border rounded' alt={p.name} src={`${process.env.PUBLIC_URL}/media/products/${p.filename}-1.png`}
                                onMouseEnter={() => setPic('-1')}
                                onMouseLeave={() => setPic('-1')}
                                style={{
                                    width: '30px',
                                    marginRight: '0.5rem',
                                    marginLeft: '1rem'
                            }}/>
                            <img className='border rounded' alt={p.name} src={`${process.env.PUBLIC_URL}/media/products/${p.filename}-2.png`}
                                onMouseEnter={() => setPic('-2')}
                                onMouseLeave={() => setPic('-1')}
                                style={{
                                    width: '30px',
                                    marginRight: '0.5rem'
                            }}/>
                            <img className='border rounded' alt={p.name} src={`${process.env.PUBLIC_URL}/media/products/${p.filename}-3.png`}
                                onMouseEnter={() => setPic('-3')}
                                onMouseLeave={() => setPic('-1')}
                                style={{
                                    width: '30px',
                                    marginRight: '0.5rem'
                            }}/>
                            <img className='border rounded' alt={p.name} src={`${process.env.PUBLIC_URL}/media/products/${p.filename}-4.png`}
                                onMouseEnter={() => setPic('-4')}
                                onMouseLeave={() => setPic('-1')}
                                style={{
                                    width: '30px',
                                    marginRight: '0.5rem'
                            }}/>
                        </div>
                    </div>
                    <h2>{p.name}</h2>
                    <div className='dropdown-divider' style={{
                        width:'16rem'
                    }}/>
                    <h4>${p.price}</h4>
                    <div style={{
                        marginRight: '10rem'
                    }}>
                       <p>{p.description}</p> 
                    </div>
                    <Link className='btn btn-warning' to='/cart' 
                        onClick={() => context.addToCart(p.id)}>Add To Cart</Link>
                </div>
                <body.Row className='py-5 text-white m-3'>
                    <h1>Hello</h1>
                </body.Row>
            </body.Container>
        )
    }
}

