import React from 'react'
import '@fortawesome/fontawesome-free/css/all.css'
import { Link } from 'react-router-dom'
import bear from './bear.PNG'
import * as bs from 'react-bootstrap'
import AppContext from './context'

export default function Top() {
    const context = React.useContext(AppContext)

    return (
        <bs.Row className='shadow-sm'>
        <bs.Navbar expand="lg">
            <Link to="/">
                    <img src={ bear } className='inline-block' alt='bear' style={{
                        height: '2.5rem',
                        paddingRight: '0.5rem'
                    }}/>
            </Link>
            <Link to='/'>
                <bs.Navbar.Brand >Arctic</bs.Navbar.Brand>
            </Link>
            <bs.Navbar.Toggle aria-controls="basic-navbar-nav" />
            <bs.Navbar.Collapse id="basic-navbar-nav">
                <bs.Nav className="mr-auto">
                    <Link to="/about" className="nav-link">About</Link>
                    <Link to="/help" className="nav-link">Help</Link>
                </bs.Nav>
                <bs.Nav>
                    <bs.Nav className="mr-auto pr-4">
                        <Link to="/cart" className="nav-link">
                            <i className='fas fa-shopping-cart m-2 inline-block' />
                            {context.cartCount}
                        </Link>
                    </bs.Nav>
                    <bs.NavDropdown title="Welcome, Kobe" alignRight>
                        <bs.NavDropdown.Item>My Account</bs.NavDropdown.Item>
                        <bs.NavDropdown.Divider />
                        <bs.NavDropdown.Item href="#action/3.4">Logout</bs.NavDropdown.Item>
                    </bs.NavDropdown>
                </bs.Nav>
            </bs.Navbar.Collapse>
        </bs.Navbar>
        </bs.Row>
    )
}
// I will need links in the nav dropdown instead of the items