import React from 'react'
import { Row } from 'react-bootstrap'
import { Col } from 'react-bootstrap'
import * as body from 'react-bootstrap'
import Category from './product-category'
//products={products}

export default function Home() {
    return (
        <body.Container fluid className='p-0 '>
            <Row noGutters style={{padding: '3rem 0', paddingTop: '2rem'}}>
                <Col>
                    <h1 className="text-center mt-5">Welcome to Arctic</h1>
                </Col>
            </Row>
            
            <Row noGutters style={{ padding: '4rem 0' }}>
                < Category/>
            </Row>
        </body.Container>
    )
}