import React from 'react'
import * as body from 'react-bootstrap'
import AppContext from './context'
import { Link } from 'react-router-dom'

export default function Cart() {
    const context = React.useContext(AppContext)
    // let sum = 0
    if (context.cartCount > 0) {
        return (
            <body.Container style={{
                paddingRight: '2rem',
                paddingLeft: '2rem'
            }}>
                <div>
                    <br/>
                    <h1 style={{
                        display: 'inline',
                        marginBottom: '1rem',
                    }}>Cart</h1>
                    <br />
                    <br />
                    <body.Table>
                        <thead>
                            <tr>
                                <th></th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}>Name</th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}>Quantity</th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}>Price</th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}>Extended</th>
                                <th></th>
                            </tr>
                        </thead>
                        <tbody>
                            {Object.entries(context.cart).map(([prod, idx]) => {
                                let p = []
                                p = context.products.find(x => x.id.toString() === prod)
                                // sum += parseFloat(p.price * idx)
                                return (
                                    <tr key={p.id}>
                                        <td>
                                            <img src={`${process.env.PUBLIC_URL}/media/products/${p.filename}-1.png`} style={{
                                                width: '50px',
                                            }} className='inline-block' alt={p.name}/>
                                        </td>
                                        <td style={{
                                            paddingTop: '1.75rem',
                                            paddingLeft: '2rem',
                                        }}>{p.name}</td>
                                        <td style={{
                                            paddingTop: '1.75rem',
                                            paddingLeft: '2rem',
                                        }}>{idx}</td>
                                        <td style={{
                                            paddingTop: '1.75rem',
                                            paddingLeft: '2rem',
                                        }}>${p.price}</td>
                                        <td style={{
                                            paddingTop: '1.75rem',
                                            paddingLeft: '2rem',
                                        }}>${(p.price * idx).toFixed(2)}</td>
                                        <td style={{
                                            paddingTop: '1.5rem',
                                            paddingLeft: '3rem'
                                        }}>
                                            <body.Button variant='outline-dark' onClick={() => context.removeFromCart(p.id)}>Remove</body.Button>
                                        </td>
                                    </tr>
                                )
                            })}
                            <tr>
                                <th></th>
                                <th style={{
                                    paddingLeft: '2rem'
                                }}>Total</th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}></th>
                                <th style={{
                                    paddingLeft: '2rem',
                                }}></th>
                                <th style={{
                                    paddingLeft: '2rem'
                                }}>${context.getTotal().toFixed(2)}</th>
                                <td style={{
                                    paddingLeft: '3rem',
                                }}>
                                    <Link className='btn btn-success' to='/checkout'>Checkout</Link>
                                </td>
                            </tr>
                        </tbody>
                    </body.Table>
                </div>
                <body.Row>
                    <div style={{
                        height: '10rem',
                    }}></div>
                </body.Row>
            </body.Container>
        )
    }
    else {
        return (
            <body.Container style={{
                padding: '2rem'
            }}>
                <h1>Cart</h1><br/>
                <p>No items currently in cart.</p>
            </body.Container>
        )
    }
}