import React from 'react'
import { useRouteMatch } from 'react-router-dom'
import ProductCard from './product-card'
import * as body from 'react-bootstrap'
import AppContext from './context'

export default function Category() {
    const context = React.useContext(AppContext)
    
    //set up useRouteMatch
    let match = useRouteMatch({path: '/category/:name', strict: true, exact: true})
    
    let cats2 = {}
    
    // if there is a match, send products to new object
    if (match) {
        let category = match.params.name 
        const cat = context.categories.filter(cat => cat.title === category)       

        // validate category
        if (cat.length > 0) {
            cat.map(x => (
                cats2 = context.products.filter(prod => prod.category === x.id)
            ))
        }
        else {
            cats2 = context.products
        }
    }
    // else just products
    else {
        cats2 = context.products
    }

    
    return (
        <>
            {Object.values(cats2).map((cat, idx) => {
                return (
                    <body.Col md='3' key={idx}>
                        <ProductCard products={cat} key={idx}/>
                    </body.Col>
                )
            })}
        </>
    )
}