import React from 'react'
import axios from 'axios'
import AppContext from './context'
import App from './App'
import produce from 'immer'

/** The context provider for our app */
export default class AppProvider extends React.Component {

    constructor(props) {
        super(props)
        this.actions = {
            addToCart : this.addToCart,
            removeFromCart : this.removeFromCart,
            viewRecent : this.viewRecent,
            getTotal : this.getTotal,
            transferCart: this.transferCart,
            clearCart: this.clearCart,
        }
        this.state = {
            categories: [],
            products: [],
            cart: {},
            cartCount: 0,
            recent: [],
        }
        // load the categories here... >> bad idea it would freeze the system
    }

    clearCart = (all) => {
        all = this.products
        this.setState(state => produce(state, draft => {
            draft.cart = {}
            draft.cartCount = 0
        }))
    }

    transferCart() {
        var sendCart = []
        for (let [prodID, qty] of Object.entries(this.cart)) {
            let prod = []
            prod = this.products.find(x => x.id.toString() === prodID)
            sendCart.push(
                {
                    'pid': prod.id.toString(),
                    'qty': qty,
                    'price': prod.price.toString()
                }
            )
        }
        return (sendCart)
    }

    getTotal() {
        let total = 0
        for (let [prodID, qty] of Object.entries(this.cart)) {
            // console.log(prodID + ': ' + qty)
            let prod = []
            prod = this.products.find(x => x.id.toString() === prodID)
            total += prod.price * qty
        }

        // console.log(total)

        return (total)
    }

    addToCart = (pID) => {
        this.setState(state => produce(state, draft => {
            if (pID in draft.cart) {
                draft.cart[pID]++
            }
            else {
                draft.cart[pID] = 1
            }

            draft.cartCount++
        }))
        // console.log(this.cart)
    }

    removeFromCart = (pID) => {
        this.setState(state => produce(state, draft => {
            delete draft.cart[pID]

            draft.cartCount = 0
            var prod = 2
            for (const p in draft.cart) {
                draft.cartCount++
                prod = prod * p
            }
        }))
    }

    viewRecent = (pID) => {
        this.setState(state => produce(state, draft => {
            if (!draft.recent.includes(pID)) {
                draft.recent.unshift(pID)
            }

            while (draft.recent.length > 5) {
                draft.recent.pop()
            }
        }))
    }

    render() {
        if (!this.state.categories) {
            return <div>Loading...</div>
        }

        return (
            <AppContext.Provider value={{...this.state, ...this.actions}}>
                <App />
            </AppContext.Provider>
        )
    }

    async componentDidMount() {
        const resp = await axios.get('http://localhost:8000/api/category/')
        const resp2 = await axios.get('http://localhost:8000/api/product/')
        
        this.setState({
            categories: resp.data, 
            products: resp2.data,
        })
    }
}