import React from 'react'
import * as bs from 'react-bootstrap'
import { Formik, Form, Field} from 'formik'
import AppContext from './context'
import axios from 'axios'
import { Elements, CardElement, useStripe, useElements } from '@stripe/react-stripe-js';
import { loadStripe } from '@stripe/stripe-js'
import { useHistory } from "react-router-dom"


const stripePromise = loadStripe('pk_test_f1xDCzlBoT3uL1aW4ZmNPYrT00dZoWb1MU')


function Checkout(props) {
    // we'll add Stripe's Elements component here later
    return (
        <Elements stripe={stripePromise}>
           <CheckoutController /> 
        </Elements>
    )
}
export default Checkout

const CARD_ELEMENT_OPTIONS = {
    style: {
      base: {
        color: "#32325d",
        fontFamily: '"Helvetica Neue", Helvetica, sans-serif',
        fontSmoothing: "antialiased",
        fontSize: "16px",
        "::placeholder": {
          color: "#aab7c4",
        },
      },
      invalid: {
        color: "#fa755a",
        iconColor: "#fa755a",
      },
    },
  }

const CheckoutController = props => {
    const context = React.useContext(AppContext)
    const total = context.getTotal() // context.getCartTotal()
    const stripe = useStripe()
    const elements = useElements()
    let sendCart = context.transferCart()
    let history = useHistory()
    return (
        <Formik
            initialValues={{
                name: 'Conrad Fox',
                address1: '1234',
                address2: '5678',
                city: 'Provo',
                state: 'UT',
                zipcode: '84602',
                total: total,
                items: sendCart,
                payment_intent: {},
            }}
            validateOnChange={false}
            validateOnBlur={false}
            validate={values => {
                const errors = {}
                if (!values.name) {
                    errors.name = 'Name is required'
                }
                if (!values.address1) {
                    errors.address1 = 'Address is required'
                }
                if (!values.state) {
                    errors.state = 'State is required'
                }
                if(!values.city) {
                    errors.city = 'City is required'
                }
                if (!values.zipcode) {
                    errors.zipcode = 'Zipcode is required'
                }
                return errors
            }}
            onSubmit={async (values, actions) => {
                const resp = await axios.post('http://localhost:8000/api/sale/', JSON.stringify(values))
                
                await new Promise(resolve => {
                    setTimeout(() => {  // wait 2 seconds, then set the form as "not submitting"
                        resolve()
                        
                    }, 2000)
                })

                const result = await stripe.confirmCardPayment(resp.data.client_secret, {
                    payment_method: {
                      card: elements.getElement(CardElement),
                      billing_details: {
                        name: values.name,
                      },
                    }
                  })

                  if (result.error) {
                    actions.setFieldError('payment', result.error.message)
                  } 
                  else {

                    if (result.paymentIntent.status === 'succeeded') {
                      console.log('success!')
                      context.clearCart()
                      history.push('/receipt')
                    }
                  }
                console.log('after the 2 seconds')
                console.log(resp.data)
            }}
        >{form => (
            <PaymentForm form={form} total={total}/>
        )}</Formik>
    )
}


/**
 * The form layout/html.
 * This component needs finishing.
 */
const PaymentForm = props => (
    <Form>
        <div className='pl-4 pt-3'>
            <h2>Checkout</h2>
        </div>
        <bs.Row>
            <bs.Col md='6'>
                <div className='pl-3 pt-3 pb-3 pr-0'>
                    <bs.Card>
                        <bs.Card.Header>
                            Shipping
                        </bs.Card.Header>
                        <bs.Card.Body>
                            <Input title="Name:" name="name" type="text" disabled={props.form.isSubmitting} />
                            <Input title='Address 1:' name='address1' type='text' disabled={props.form.isSubmitting} />
                            <Input title='Address 2:' name='address2' type='text' disabled={props.form.isSubmitting} />
                            <Input title='City:' name='city' type='text' disabled={props.form.isSubmitting} />
                            <Input title='State:' name='state' type='text' disabled={props.form.isSubmitting} />
                            <Input title='Zip:' name='zipcode' type='text' disabled={props.form.isSubmitting} />
                        </bs.Card.Body>
                    </bs.Card>
                </div>
            </bs.Col>
            
            <bs.Col md='6'>
                <div className='pr-3 pl-0 pb-3 pt-3'>
                    <bs.Card name='cardErrors'>
                        <bs.Card.Header>
                            Payment
                        </bs.Card.Header>
                        <bs.Card.Body>
                        <Field name='payment' disabled={props.form.isSubmitting}>
                            {rProps => (
                            <bs.Form.Group>
                                <CardElement options={CARD_ELEMENT_OPTIONS} disabled={props.form.isSubmitting}/>
                                {rProps.meta.error &&
                                    <div className="text-danger">{rProps.meta.error}</div>}
                            </bs.Form.Group>
                            )}
                        </Field>
                        </bs.Card.Body>
                    </bs.Card>
                </div>
                <p>Your card will be charged ${props.total.toFixed(2)}</p>
                { 
                    props.form.isSubmitting? 
                    <bs.Button type='submit' variant='success' disabled={props.form.isSubmitting}>
                        <bs.Spinner 
                            as="span"
                            animation="border"
                            size="sm"
                            role="status"
                            aria-hidden="true"
                        />
                        Purchase
                    </bs.Button> :
                    <bs.Button type='submit' variant='success' disabled={props.form.isSubmitting}>Purchase</bs.Button>
                }
                
            </bs.Col>
        </bs.Row>
    </Form>
)


/**
 * A form input.
 *   props.title - the title that shows above the input box
 *   props.type - the type of input (see React Bootstrap Form.Control)
 *   props.placeholder - placeholder text in the input.
 * This component is finished and doesn't need additional work.
 */
const Input = (props) => (
    <Field name={props.name}>{rProps => (
        <bs.Form.Group>
            {props.title &&
                <bs.Form.Label>{props.title}</bs.Form.Label>
            }
            <bs.Form.Control
                type={props.type}
                placeholder={props.placeholder}
                {...rProps.field}
                disabled={props.disabled}
            />
            {rProps.meta.touched && rProps.meta.error &&
                <div className="text-danger">{rProps.meta.error}</div>
            }
        </bs.Form.Group>
    )}</Field>
)